import { Rastro } from './../../models/rastro';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

/**
 * Generated class for the RastroGridPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-rastro-grid',
  templateUrl: 'rastro-grid.html',
})
export class RastroGridPage {
  public rastro: Rastro[];

  constructor(public navCtrl: NavController, public navParams: NavParams) {
    // this.veiculo = this.navParams.get("veiculo");
    this.rastro = this.navParams.get("rastro");
    
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad RastroGridPage');
  }

}



